import Layout from '../../components/layout';
import fetch from 'isomorphic-unfetch';
import {API_URL, PRODUCTS} from "../../assets/config/api";
import React from 'react';

export default class View extends React.Component {
    static getInitialProps({req}) {
        console.log(req.query.id);
        const res = fetch(API_URL + PRODUCTS + 'getById', {
            method: 'POST',
            body: JSON.stringify({id: req.query.id}),
            headers: {
                'Content-Type': 'application/json'
            },
        }).then((response) => {
            return response.json();
        }).then((product)=>{
            console.log(product);
        });
        // const data = res.json();
        // console.log(data);
        // return {product: data.product};
        return {};
    }

    render() {
        return (
            <Layout>
                <h3 style={{textAlign: 'center'}}>Product View</h3>
                <hr/>
                <h5>{this.props.product.title}</h5>
                <h6>Specification</h6>
                <ul>
                    {this.props.product.specification.map((element) => (
                        <li key={element.name}>{element.name}:{element.value}</li>
                    ))}
                </ul>
            </Layout>
        );
    }
}


